.hide{
  overflow: hidden;
  opacity: 0;
  transition: all 2s ease-in-out;
  height: 0;
  display: none;
  }
.vp-search__date{
  margin-top: 10px;
  padding: 0 10px;
  background: #fff;

  visibility: hidden;
  opacity: 0;
  height: 0;
  transform: scale(0) translateY(-100px);
  overflow-y: visible;
  overflow-x: hidden;
  transition: all 600ms ease-in-out;
  }
.vp-search__date.focus{
  visibility: visible;
  opacity:1;
  height: 200px;
  transform: scale(1) translateY(0px);
  }
.marking{
  color: black;
  }
ul{
  display: flex;
  align-items: flex-start;
  flex-direction: column;


  &::-webkit-scrollbar {
    width: 10px;
    background-color: #000000;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.2);

    }
  li{
    width: 100%;
    list-style: none;
    margin-bottom: 4px;
    border-top: 1px solid #2f80ed;
    &:last-child{
      margin-bottom: 0;
      }
    a{
      font-family: sans-serif;
      color: #1a3246;
      width: 100%;
      display: flex;
      align-items: center;
      padding: 2px 0;
      height: 20px;
      }
    }
  }