.vp-slider-wrapper{
  position: relative;
  min-height: 350px;
  //display: flex;
  //align-items: center;
  //justify-content: center;
  .vp-slider{
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 300px;
	overflow: hidden;
	.vp-slider-inner{
	  position: absolute;
	  top: 0;
	  left: 0;
	  height: 100%;
	  width: 200%;
	  display: grid;
	  grid-template-columns: repeat(7, 1fr);
	  gap: 5px;
	  pointer-events: none;
	  transition: 0s easy-in;
	  .vp-slide{
		height: 300px;
		.touch-image{
		  object-fit: contain;
		  height: 100%;
		  width: 320px;
		  display: flex;
		  align-items: center;
		  justify-content: center;
		  text-align: center;
		  pointer-events: auto;
		  cursor:pointer;
		}
	  }
	}
  }
}
